
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
components: {
Group,
XInput,
XButton,
Radio
},
data() {
return {
customMoney: null,
selectedRate: null,
rateList: [
{ key: 0.1, value: "" },
{ key: 0.2, value: "" },
{ key: 0.3, value: "" },
{ key: 0.4, value: "" },
{ key: 0.5, value: "" },
{ key: 0.6, value: "" },
{ key: 0.7, value: "" },
{ key: 0.8, value: "" },
{ key: 0.9, value: "" }
]
};
},
methods: {
change(val, label) {

this.$emit("submit", val,label);
},
submit() {

if (this.customMoney) {
this.$emit("submit", this.customMoney,'');
}
}
}
};



