
import MoneyRate from "../components/MoneyRate"
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
MoneyRate,
PopupPicker,
XInput,
Group,
Cell,
XButton,
},
mounted(){
let initalLoanYears=[];
for (let i=1;i<31;i++){
initalLoanYears.push(i+""+"("+i*12+")")
}
this.sloanYearsList.push(initalLoanYears);
this.gloanYearsList.push(initalLoanYears);
},
data() {
return {
sloanMoney:100,
gloanMoney:100,
sloanYearsList:[],
sloanYear:["20(240)"],
gloanYearsList:[],
gloanYear:["20(240)"],
sshowRate:'4.90%',
gshowRate:'3.25%',
sactRate:'0.049',
gactRate:'0.0325',
showRatePage:false,
baseRate:""
};
},
methods:{
caculateLoan() {
let smonth = this.sloanYear[0].match(/\((\S*)\)/);
let smonths = smonth[1].substr(0,smonth[1].length-1);
let gmonth = this.gloanYear[0].match(/\((\S*)\)/);
let gmonths = gmonth[1].substr(0,gmonth[1].length-1);
this.$router.push({
path: "/ZuhePayment",
query: {
sloanMoney:this.sloanMoney,//
gloanMoney:this.gloanMoney,//
smonths:smonths,//
gmonths:gmonths,//
srate:this.sactRate,//
grate:this.gactRate,//
}
});
},
RateSubmit(showValue,actRate){
this.showRatePage=false;
this.actRate =actRate;
if(this.baseRate == 4.90/100){
this.sshowRate = showValue;
this.sactRate = actRate
}else if(this.baseRate == 3.25/100){
this.gshowRate = showValue;
this.gactRate = actRate;
}
},
srateClick(){
this.showRatePage = true;
this.baseRate = 4.90/100;
},
grateClick(){
this.showRatePage = true;
this.baseRate = 3.25/100;
},
}
};



