
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
components: {
Group,
XInput,
XButton,
Radio
},
props: {
baseRate:0
},
data() {
return {
customRate: null,
selectedRate: null,
rateList: [
{ key: 1, value: "" },
{ key: 0.7, value: "7" },
{ key: 0.85, value: "85" },
{ key: 0.88, value: "88" },
{ key: 0.9, value: "9" },
{ key: 0.95, value: "95" },
{ key: 1.05, value: "1.05" },
{ key: 1.1, value: "1.1" },
{ key: 1.2, value: "1.2" },
{ key: 1.3, value: "1.3" }
]
};
},
methods: {
change(val, label) {
let actRate = this.baseRate*val
this.$emit("submit", (actRate*100).toFixed(2)+"%",actRate);
},
submit() {
if (this.customRate) {
this.customRate = Number(this.customRate).toFixed(2);
this.$emit("submit", this.customRate+"%",this.customRate/100);
}
}
}
};



