import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
components: {
PopupPicker,
XInput,
Group,
Cell,
XButton,
XSwitch
},
data() {
return {
natureList: [["", ""]],
nature: [""],
houseArea: 100,
areaPrice: 10000,
stringValue: true,
IsCommonHouse:"",
};
},
methods: {
calculateShui() {
this.nature[0] == "" ? this.IsCommonHousea=1 : this.IsCommonHousea=0;
this.$router.push({
path: "/ShuifeiPay",
query: { totalPrice: this.houseArea*this.areaPrice,
IsCommonHouse:this.IsCommonHouse,
IsOnly:this.stringValue}
});
}
},
};



